.steps {
	overflow: hidden;
	padding: 30px 0 0;

	@media (min-width: 768px) {
		padding: 40px 0;
	}

	@media (min-width: 992px) {
		padding: 60px 0;
	}

	@media (min-width: 1200px) {
		padding: 80px 0;
	}
}

.steps__title {
	color: rgb(100, 69, 141);
}

.steps__wrapper {
	@media (max-width: 991px) {
		overflow-y: hidden;
		overflow-x: scroll;
		position: relative;
		width: calc(100% + 18rem);
		left: -10rem;
		padding-left: 10rem;
	}

	@media (max-width: 767px) {
		width: calc(100% + 14rem);
		left: -8rem;
		padding-left: 8rem;
		padding-right: 8rem;
	}

	@media (max-width: 575px) {
		width: calc(100% + 1.5rem);
		left: calc(-1.5rem / 2);
		padding-left: 0.75rem;
	}
}

.steps__items {
	display: flex;
	flex-wrap: nowrap;
	position: relative;
	padding-top: 80px;
	padding-bottom: 20px;
	width: 100%;

	@media (min-width: 576px) {
		padding-top: 80px;
		padding-bottom: 20px;
	}

	@media (min-width: 992px) {
		padding-top: 40px;
		flex-wrap: wrap;
	}

	@media (min-width: 1400px) {
		flex-wrap: nowrap;
		padding-top: 80px;
		padding-bottom: 0;
	}

	@media (min-width: 1600px) {
		padding-top: 40px;
	}

	@media (min-width: 1920px) {
		padding-top: calc(10px + 5vw);
	}
}

.steps__item {
	padding-right: 47px;

	@media (min-width: 2560px) {
		padding-right: 60px;
	}

	&:nth-child(1) {
		@media (min-width: 992px) {
			width: 40%;
		}

		@media (min-width: 1400px) {
			width: auto;
		}

		@media (min-width: 1600px) {
			width: 20%;
		}

		.steps__line {
			width: 218px;
			background: linear-gradient(90.00deg, rgb(201, 244, 253) 2%,rgb(208, 230, 253) 100%);

			@media (min-width: 992px) {
				width: 100%;
			}

			@media (min-width: 1400px) {
				width: 195px;
			}

			@media (min-width: 1600px) {
				width: 218px;
			}

			@media (min-width: 1600px) {
				width: 100%;
			}

			&::before {
				background-image: url('../img/icons/mini-arrow-1.svg');
			}
		}

		.steps__text {
			@media (min-width: 1600px) {
				max-width: 75%;
			}
		}
	}

	&:nth-child(2) {
		@media (min-width: 992px) {
			width: 60%;
		}

		@media (min-width: 1400px) {
			width: auto;
		}

		@media (min-width: 1600px) {
			width: 24%;
		}

		.steps__line {
			width: 249px;
			background: linear-gradient(90.00deg, rgb(210, 227, 253) 2%,rgb(220, 207, 252) 100%);

			@media (min-width: 992px) {
				width: 100%;
			}

			@media (min-width: 1400px) {
				width: 220px;
			}

			@media (min-width: 1600px) {
				width: 249px;
			}

			@media (min-width: 1600px) {
				width: 100%;
			}

			&::before {
				background-image: url('../img/icons/mini-arrow-2.svg');
			}
		}

		.steps__text {
			@media (min-width: 1600px) {
				max-width: 62%;
			}
		}
	}

	&:nth-child(3) {
		padding-right: 56px;

		@media (min-width: 992px) {
			margin-top: 30px;
			width: 60%;
		}

		@media (min-width: 1400px) {
			width: auto;
			margin-top: 0;
		}

		@media (min-width: 1600px) {
			width: 26%;
		}

		@media (min-width: 2560px) {
			padding-right: 80px;
		}

		.steps__line {
			width: 330px;
			background: linear-gradient(90.00deg, rgb(221, 206, 252) 2%,rgb(234, 200, 253) 100%);

			@media (min-width: 992px) {
				width: 100%;
			}

			@media (min-width: 1400px) {
				width: 280px;
			}

			@media (min-width: 1600px) {
				width: 330px;
			}

			@media (min-width: 1600px) {
				width: 100%;
			}

			&::before {
				right: -38px;
				background-image: url('../img/icons/mini-arrow-3.svg');

				@media (min-width: 1921px) {
					right: calc(-27px - 0.75vw);
				}

				@media (min-width: 2560px) {
					right: -64px;
				}
			}
		}

		.steps__text {
			max-width: 250px;

			@media (min-width: 992px) {
				max-width: 80%;
			}

			@media (min-width: 1400px) {
				max-width: 250px;
			}

			@media (min-width: 1600px) {
				max-width: 100%;
			}
		}
	}

	&:nth-child(4) {
		@media (min-width: 992px) {
			margin-top: 30px;
			width: 100%;
		}

		@media (min-width: 1400px) {
			width: auto;
			margin-top: 0;
		}

		@media (min-width: 1600px) {
			width: 32%;
		}

		.steps__line {
			width: 506px;
			background: linear-gradient(90.00deg, rgb(236, 200, 253) 2%,rgb(243, 196, 251) 100%);

			@media (max-width: 767px) {
				width: 330px;
			}

			@media (min-width: 768px) {
				width: 430px;
			}

			@media (min-width: 992px) {
				width: 100%;
			}

			@media (min-width: 1400px) {
				width: 330px;
			}

			@media (min-width: 1600px) {
				width: 100%;
			}
		}

		.steps__text {
			max-width: 268px;

			@media (min-width: 992px) {
				max-width: 70%;
			}

			@media (min-width: 1400px) {
				max-width: 250px;
			}

			@media (min-width: 1600px) {
				max-width: 90%;
			}
		}
	}

	&:last-child {
		padding-right: 0;
		position: relative;

		@media (min-width: 576px) {
			padding-right: 120px;
		}

		@media (min-width: 768px) {
			padding-right: 160px;
		}

		@media (min-width: 1400px) {
			padding-right: 95px;
			flex-grow: 1;
		}

		@media (min-width: 2560px) {
			padding-right: 70px;
		}

		&::before {
			position: absolute;
			content: '';
			top: 50%;
			transform: translateY(-68%) rotate(4deg);
			right: -133px;
			width: 245px;
			height: 235px;
			background-color: transparent;
			background-image: url('../../img/decor/star.png');
			background-repeat: no-repeat;
			background-position: center right -26px;
			background-size: contain;
			z-index: 2;

			@media (min-width: 576px) {
				top: 50%;
				right: -75px;
				background-position: center right 36px;
				transform: translateY(-65%) rotate(4deg);
				width: 245px;
				height: 235px;
			}

			@media (min-width: 768px) {
				top: 50%;
				transform: translateY(-66%) rotate(4deg);
				right: 65px;
			}

			@media (min-width: 992px) {
				top: 50%;
				transform: translateY(-52%) rotate(4deg);
				right: -85px;
				width: 460px;
				height: 460px;
			}

			@media (min-width: 1400px) {
				top: 50%;
				transform: translateY(-64%) rotate(4deg);
				right: -120px;
				width: 315px;
				height: 330px;
			}

			@media (min-width: 1600px) {
				top: 50%;
				transform: translateY(-62%) rotate(4deg);
				right: -187px;
				width: 344px;
				height: 365px;
			}

			@media (min-width: 1700px) {
				transform: translateY(-59%) rotate(4deg);
			}

			@media (min-width: 1921px) {
				width: calc(344px + 13vw);
				height: calc(365px + 13vw);
				right: calc(-178px - 9vw);
				top: 50%;
				transform: translateY(calc(-50% - 5px)) rotate(4deg);
			}

			@media (min-width: 2560px) {
				width: 723px;
				height: 743px;
				right: calc(-461px);
				transform: translateY(-50%) rotate(4deg);
			}
		}

		.steps__line {
			&::before {
				content: none;
			}
		}
	}
}

.steps__line {
	display: block;
	border-radius: 36.48px;
	height: 10px;
	background: linear-gradient(90.00deg, rgb(221, 206, 252) 2%,rgb(234, 200, 253) 100%);
	position: relative;

	@media (min-width: 1921px) {
		height: calc(-35px + 2.5vw);
	}

	@media (min-width: 2560px) {
		max-height: 25px;
	}

	&::before {
	  position: absolute;
	  content: '';
	  top: calc(50%);
		transform: translateY(-50%);
	  right: -33px;
	  width: 18px;
	  height: calc(100% * 2);
	  background-color: transparent;
		background-image: url('../img/icons/mini-arrow-2.svg');
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain;

		@media (min-width: 1921px) {
			width: 1.5vw;
			height: 1.5vw;
			right: calc(-33px - 0.30vw);
		}

		@media (min-width: 2560px) {
			width: 45px;
			height: 45px;
			right: -52px;
		}
	}
}

.steps__text {
	margin-top: 16px;
	font-size: 16px;
	font-weight: 500;
	line-height: 155.556%;
	max-width: 195px;

	@media (min-width: 992px) {
		max-width: 100%;
		margin-top: 24px;
	}

	@media (min-width: 1400px) {
		max-width: 195px;
	}
}