.regions-work {
	overflow: hidden;
}

.regions-work__bg {
	padding: 30px 0;
	background-image: url('../img/decor/regions-work-bg.png');
	background-repeat: no-repeat;
	height: 100%;
	background-size: cover;
	background-position: top 0 left 54%;
	position: relative;
	color: #fff;

	@media (min-width: 768px) {
		padding: 40px 0;
	}

	@media (min-width: 992px) {
		padding: 60px 0;
	}

	@media (min-width: 1200px) {
		padding: 80px 0;
	}

	@media (min-width: 1600px) {
		padding: 65px 0;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}
}

.regions-work__slogan {
	font-size: 27px;
	line-height: 110%;
	font-weight: 300;
	margin-bottom: 30px;

	@media (min-width: 992px) {
		font-size: 36px;
	}

	@media (min-width: 1200px) {
		max-width: 65%;
	}

	@media (min-width: 1400px) {
		font-size: 40px;
		max-width: 80%;
		margin-bottom: 70px;
		margin-top: 30px;
	}

	@media (min-width: 1700px) {
		font-size: 55px;
		max-width: 80%;
		margin-bottom: 70px;
		margin-top: 30px;
	}

	span {
		display: block;
		font-weight: 800;
		color: rgb(190, 120, 255);

		@media (min-width: 1200px) {
			display: inline;
		}
	}
}

.regions-work__image {
	margin-top: -20px;
	position: relative;
	height: 54vw;

	@media (min-width: 576px) {
		margin-top: -44px;
		height: 270px;
	}

	@media (min-width: 768px) {
		height: 380px;
	}

	@media (min-width: 992px) {
		height: 0;
	}

	img {
		max-width: calc(100% + 60vw);
		object-fit: contain;
		position: relative;
		left: calc(-50% + 3vw);

		@media (min-width: 576px) {
			max-width: calc(100% + 305px);
			left: calc(-50% + 25px);
		}

		@media (min-width: 768px) {
			max-width: calc(100% + 430px);
			left: calc(-50% + 25px);
		}

		@media (min-width: 992px) {
			max-width: calc(100% + 435px);
			left: calc(-50% - 45px);
			top: 38px;
		}

		@media (min-width: 1200px) {
			top: -43px;
			max-width: calc(100% + 535px);
			left: calc(-50% - 132px);
		}

		@media (min-width: 1400px) {
			top: -26px;
			max-width: calc(100% + 675px);
			left: calc(-50% - 175px);
		}

		@media (min-width: 1700px) {
			top: -59px;
		}
	}
}

.regions-work__text {
	position: relative;
	z-index: 2;

	@media (min-width: 1200px) {
		max-width: 88%;
	}

	@media (min-width: 1600px) {
		max-width: 75%;
	}

	p {
		&:last-child {
			margin-bottom: 0;
		}
	}
}

.regions-work__about-row {
	margin-top: 30px;

	@media (min-width: 768px) {
		margin-top: 45px;
	}

	@media (min-width: 1200px) {
		margin-top: -70px;
	}

	@media (min-width: 1400px) {
		margin-top: -35px;
	}

	[class^="col"] {
		&:not(:last-child) {
			margin-bottom: 32px;

			@media (min-width: 992px) {
				margin-bottom: 0;
			}

			@media (min-width: 1200px) {
				order: 2;
			}
		}

		@media (min-width: 1200px) {
			order: 1;
		}
	}
}

.regions-work__about-wrap {
	position: relative;
	background: inherit;
	border-radius: 20px;
	overflow: hidden;
	box-shadow: 0 28.04px 22.78px 0 rgba(67, 13, 112, 0.15);
	border: 1px solid rgba(164, 71, 255, 0.5);

	@media (min-width: 992px) {
		border-radius: 100px;
	}

	@media (min-width: 1200px) {
		margin-left: 50px;
	}
}


.regions-work__about {
	position: relative;
	width: 100%;
	height: 100%;
	z-index: 1;
	display: flex;
	flex-wrap: nowrap;
	padding: 12px 18px 18px 10px;

	@media (min-width: 576px) {
		padding: 20px 28px 28px 18px;
	}

	@media (min-width: 992px) {
		padding: 20px 28px 20px 18px;
	}

	&::before {
		content: '';
		width: calc(100% + 50px);
		height: calc(100% + 50px);
		background: inherit;
		position: absolute;
		left: -25px;
		top: -25px;
		box-shadow: inset 0 0 0 200px rgba(255, 255, 255, 0.12);
		filter: blur(10px);
	}

	& > * {
		position: relative;
		z-index: 1;
	}
}

.regions-work__about-icon {
	@media (min-width: 1200px) {
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	.btn {
		font-size: 16px;
		padding: 10px 24px;
		max-height: 46px;
		max-width: fit-content;

		@media (min-width: 1400px) {
			font-size: 18px;
			padding: 14px 33px;
			max-height: 56px;
		}
	}

	.btn--icon {
		border-radius: 50%;
		min-width: 50px;
		min-height: 50px;
		max-width: 50px;
		max-height: 50px;

		@media (min-width: 768px) {
			min-width: 75px;
			min-height: 75px;
			max-width: 75px;
			max-height: 75px;
		}

		@media (min-width: 992px) {
			min-width: 132px;
			min-height: 132px;
			max-width: 132px;
			max-height: 132px;
		}
	}

	.tg-icon {
		padding: 0;

		svg {
			top: 52%;
			left: 49%;
			width: 26px;
			height: 26px;

			@media (min-width: 768px) {
				top: 50%;
				left: 48%;
				width: 40px;
				height: 40px;
			}

			@media (min-width: 992px) {
				top: 51%;
				left: 47%;
				width: 70px;
				height: 70px;
			}
		}
	}
}

.regions-work__about-info {
	margin-left: 27px;

	p {
		&:last-child {
			margin-bottom: 0;
		}
	}
}

.regions-work__about-title {
	font-size: 16px;
	font-weight: 700;
	margin-bottom: 10px;

	@media (min-width: 768px) {
		font-size: 20px;
	}

	@media (min-width: 992px) {
		margin-bottom: 15px;
	}

	@media (min-width: 1200px) {
		margin-bottom: 10px;
	}
}

.regions-work__about-text {
	font-size: 12px;

	@media (min-width: 768px) {
		font-size: 14px;
		line-height: 130%;
	}

	@media (min-width: 1400px) {
		font-size: 16px;
	}

}

.regions-work__buttons {
	@media (min-width: 768px) {
		display: flex;
		flex-wrap: wrap;
		gap: 20px;
	}

	@media (min-width: 992px) {
		height: 100%;
		flex-direction: column;
		justify-content: center;
	}

	@media (min-width: 1200px) {
		flex-direction: row;
		justify-content: flex-start;
	}

	.btn {
		@media (max-width: 359px) {
			padding: 11px 24px;
		}

		@media (min-width: 768px) {
			flex-grow: 1;
		}

		@media (min-width: 992px) {
			flex-grow: 0;
			max-width: 230px;
		}

		@media (min-width: 1200px) {
			height: 60px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			margin-top: auto;
			padding: 11px 36px;
			max-width: unset;
		}

		@media (min-width: 1400px) {
			height: 80px;
		}

		@media (min-width: 1700px) {
			padding: 11px 46px;
		}

		&:not(:last-child) {
			margin-right: 32px;

			@media (min-width: 768px) {
				margin-right: 0;
			}

			@media (min-width: 1200px) {
				margin-right: 32px;
			}
		}
	}
}