.main-hero {
  position: relative;
  overflow: hidden;

  @media (min-width: 768px) {
    padding-top: 90px !important;
  }

  @media (min-width: 992px) {
    padding-top: 120px !important;
  }

  @media (min-width: 1200px) {
    padding-top: 145px !important;
  }

  @media (min-width: 1400px) {
    padding-top: 160px !important;
  }

  @media (min-width: 1600px) {
    padding-top: 145px !important;
  }

  @media (min-width: 1900px) {
    //padding-top: 230px !important;
  }

  @media (min-width: 1600px) {
    height: 100vh;
    min-height: 890px;
    //max-height: 1000px;
  }

  @media (min-width: 1900px) {
    //max-height: 1440px;
  }

  &::before {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-image: url('../img/decor/circles.svg');
    background-repeat: no-repeat;
    background-position: top -88px right -470px;
    background-size: 950px;

    @media (min-width: 576px) {
      right: 0;
      background-position: top -140px right -522px;
      background-size: 1200px;
    }

    @media (min-width: 768px) {
      background-position: bottom -422px right -215px;
      background-size: 1150px;
      right: calc(50% - 406px);
    }

    @media (min-width: 1200px) {
      background-position: bottom -545px right -248px;
      background-size: 1272px;
      right: calc(50% - 490px);
    }

    @media (min-width: 1600px) {
      background-position: bottom -829px right -624px;
      background-size: 2000px;
    }
  }

  @media (min-width: 768px) {
    position: relative;
  }

  & > .container {
    @media (min-width: 1600px) {
      height: 100%;
    }

    & > .row {
      @media (min-width: 1600px) {
        height: 100%;
      }
    }
  }

}

.main-hero__col-1,
.main-hero__col-2 {
  @media (min-width: 768px) {
    &::before {
      position: absolute;
      content: '';
      top: 0;
      height: 100%;
    }
  }

  @media (min-width: 1600px) {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  & > * {
    position: relative;
    z-index: 2;
  }
}

.main-hero__col-1 {
  position: relative;
  padding-bottom: 17px;

  @media (min-width: 576px) {
    padding-bottom: 32px;
  }

  @media (min-width: 768px) {
    width: var(--main-hero__col-1);
  }

  @media (min-width: 992px) {
    padding-bottom: 60px;
  }

  @media (min-width: 1600px) {
    position: static;
  }

  &::before {
    position: absolute;
    content: '';
    bottom: 0;
    right: -65px;
    width: 100%;
    height: 100%;
    background-image: url('../img/decor/rocket.png');
    background-repeat: no-repeat;
    background-position: bottom right;
    background-size: 100%;

    @media (min-width: 576px) {
      right: calc(50% - 60vw);
    }

    @media (min-width: 768px) {
      right: calc(50% - 384px);
      background-size: 470px;
      transform: scale3d(1.2, 1.2, 1) translateY(-12px);
      z-index: 2;
    }

    @media (min-width: 992px) {
      right: calc(50% - 330px);
      transform: scale3d(1.2, 1.2, 1) translateY(-51px);
    }

    @media (min-width: 1200px) {
      right: calc(50% - 266px);
      transform: scale3d(1.5, 1.5, 1) translateY(-104px);
    }

    @media (min-width: 1400px) {
      right: calc(50% - 390px);
    }

    @media (min-width: 1600px) {
      right: calc(50% - 330px);
      background-size: 825px;
      transform: none;
    }

    @media (min-width: 1900px) {
      //right: calc(50% - 660px);
      //background-size: 1000px;
      background-size: 825px;
      background-size: 43vw;
    }

    @media (min-width: 2560px) {
      background-size: 1050px;
    }
  }

}

.main-hero__col-1-wrap {
  
}

.main-hero__col-2 {
  position: relative;
  padding-top: 32px;
  padding-bottom: 21px;

  @media (min-width: 576px) {
    padding-bottom: 40px;
  }

  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-image: url('../img/decor/main-hero-back.png');
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;

    @media (min-width: 576px) {
      left: -16vw;
      width: calc(100% + (16vw * 2));
    }

    @media (min-width: 768px) {
      left: auto;
      right: 0;
      width: var(--main-hero__col-2);
    }

    @media (min-width: 992px) {
      left: calc(50% + 122px);
      width: calc(var(--main-hero__col-2) + 100px);
    }

    @media (min-width: 1200px) {
      left: calc(50% + 146px);
      width: calc(var(--main-hero__col-2) + 100px);
    }

    @media (min-width: 1400px) {
      left: calc(50% + 138px);
    }

    @media (min-width: 1700px) {
      left: calc(50% + 138px);
    }

    @media (min-width: 2560px) {
      width: calc(var(--main-hero__col-2) + 230px);
    }
  }

  @media (min-width: 768px) {
    width: var(--main-hero__col-2);
    position: static;
    padding-top: 0;
  }
}

.main-hero__col-2-wrap {
  @media (min-width: 768px) {
    max-width: 250px;
    margin-left: auto;
  }

  @media (min-width: 992px) {
    max-width: 320px;
  }

  @media (min-width: 1200px) {
    max-width: 370px;
  }

  @media (min-width: 1400px) {
    max-width: 440px;
  }

  @media (min-width: 1700px) {
    max-width: 524px;
  }
}

.main-hero__title {
  margin-bottom: 16px;

  @media (max-width: 575px) {
    font-size: 24px;
    line-height: 117%;
  }

  @media (min-width: 768px) {
    margin-bottom: 36px;
  }
}

.main-hero__features {
  list-style: none;
  padding: 0;
  margin: 0;

  li {
    border: 0.63px solid rgba(154, 154, 200, 0.29);
    border-radius: 24px;
    padding: 0px 12px;
    box-shadow: 0 13px 33px 0 rgba(105, 21, 188, 0.08);
    background: #fff;
    font-weight: 500;
    font-size: 12px;
    line-height: 248%;
    color: #6c6db0;
    margin-bottom: 5px;

    @media (min-width: 576px) {
      font-size: 14px;
      margin-bottom: 10px;
    }

    @media (min-width: 768px) {
      font-size: 16px;
    }

    @media (min-width: 992px) {
      font-size: 18px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &.list-style-squares {
    li {
      position: relative;
      padding-left: 30px;
      max-width: max-content;

      &::before {
        position: absolute;
        content: '';
        top: 50%;
        transform: translateY(-50%);
        left: 12px;
        width: 0.467rem;
        height: 0.467rem;
        background-image: url('../img/icons/square.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;

        @media (min-width: 768px) {
          width: 0.667rem;
          height: 0.667rem;
        }
      }
    }
  }
}

.main-hero__register {
  color: #fff;

  .main-hero__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 88%;
    margin-bottom: 18px;

    @media (min-width: 768px) {
      font-size: 22px;
    }

    @media (min-width: 992px) {
      font-size: 28px;
      margin-bottom: 37px;
    }

    @media (min-width: 1200px) {
      font-size: 30px;
    }

    @media (min-width: 1400px) {
      font-size: 39px;
    }
  }

  form {
    label {
      width: 100%;

      input {
        width: 100%;
      }

      &:not(:last-of-type) {
        margin-bottom: 13px;

        @media (min-width: 992px) {
          margin-bottom: 20px;
        }
        @media (min-width: 1200px) {
          margin-bottom: 30px;
        }
      }
    }

    button {
      margin-top: 19px;

      @media (min-width: 768px) {
        width: 100%;
      }

      @media (min-width: 992px) {
        max-width: 330px;
      }

      @media (min-width: 1200px) {
        margin-top: 30px;
      }
    }

    .form-decs {
      margin-top: 15px;
      font-size: 8px;
      line-height: 100%;

      @media (min-width: 768px) {
        font-size: 10px;
      }

      @media (min-width: 992px) {
        font-size: 12px;
        max-width: 330px;
        line-height: 110%;
        font-weight: 300;
      }

      @media (min-width: 1200px) {
        font-size: 16px;
        margin-top: 30px;
        line-height: 120%;
      }

      @media (min-width: 1400px) {
        margin-top: 50px;
      }
    }
  }
}