.our-partners {
	overflow: hidden;
	padding: 30px 0;

	@media (min-width: 768px) {
		padding: 40px 0;
	}

	@media (min-width: 992px) {
		padding: 60px 0;
	}

	@media (min-width: 1200px) {
		padding: 80px 0;
	}
}

.our-supply + .our-partners {
	padding: 0 0 30px;

	@media (min-width: 576px) {
		padding: 20px 0 30px;
	}

	@media (min-width: 768px) {
		padding: 20px 0 40px;
	}

	@media (min-width: 992px) {
		padding: 20px 0 60px;
	}

	@media (min-width: 1200px) {
		padding: 30px 0 80px;
	}
}

.our-partners__title {
	color: rgb(100, 69, 141);
}

.our-partners__subtitle {
	max-width: 80%;

	@media (min-width: 1200px) {
		max-width: unset;
	}
}

.our-partners__wrapper {
	position: relative;
	width: calc(100% + 1.5rem);
	left: -0.75rem;
	padding-left: 0.75rem;
	margin-top: 20px;

	@media (min-width: 992px) {
		position: static;
		width: 100%;
		left: 0;
		padding: 0;
		margin: 0;
	}
}


.partnersSwiper {
	overflow: unset;

	.swiper-wrapper {
		padding-bottom: 75px;

		@media (min-width: 576px) {
			padding-top: 20px;
			padding-bottom: 95px;
		}

		@media (min-width: 1200px) {
			padding-top: 30px;
			padding-bottom: 130px;
		}

		@media (min-width: 1700px) {
			padding-top: 50px;
			padding-bottom: 140px;
		}

		@media (min-width: 2560px) {
			padding-top: 90px;
			padding-bottom: 180px;
		}
	}

	.swiper-slide {
		//display: flex;
		//flex-direction: column;
		//justify-content: center;
		height: 100%;
		align-self: center;

		@media (max-width: 1199px) {
			width: calc(100% - 800px) !important;
		}

		@media (max-width: 991px) {
			width: calc(100% - 500px) !important;
		}

		@media (max-width: 767px) {
			width: calc(100% - 350px) !important;
		}

		@media (max-width: 575px) {
			width: calc(100% - 225px) !important;
		}

		@media (max-width: 424px) {
			width: calc(100% - 150px) !important;
		}


		img {
			@media (min-width: 768px) {
				object-fit: contain;
				min-width: 160px;
				height: 66px;
			}

			@media (min-width: 2560px) {
				object-fit: contain;
				min-width: 180px;
				height: 80px;
			}
		}
	}

	.swiper-scrollbar__wrap {
		position: relative;
		width: calc(100% - 130px);
		left: 50%;
		transform: translateX(-52.5%);
		bottom: 40px;

		@media (min-width: 576px) {
			transform: translateX(-51%);
		}

		@media (min-width: 1200px) {
			transform: translateX(-50.6%);
			bottom: 50px;
			width: calc(100% - 160px);
		}
	}

	.swiper-scrollbar {
		&::before, &::after {
			width: 70px;
			display: none;

			@media (min-width: 768px) {
				width: 75px;
			}
		}

		.swiper-scrollbar-drag {
			//background-color: transparent;

			&::before {
			  position: absolute;
			  //content: '';
			  top: 0;
			  left: 50%;
				//left: 0;
				transform: translateX(-50%);
			  width: 25%;
			  height: 100%;
				background-color: rgb(108, 109, 176);
			}
		}
	}

	.swiper-button-next,
	.swiper-button-prev {
		top: auto;
		bottom: 24px;
	}

	.swiper-button-next {
		right: 0.75rem;

		@media (min-width: 576px) {
			right: 0.75rem;
		}
	}

	.swiper-button-prev {
		left: 0;
	}
}


.our-partners__item {
	display: flex;
	flex-direction: column;
	justify-content: center;
	height: 100%;
}

.our-partners__icon {
	max-width: 70%;
	height: 100%;

	@media (min-width: 768px) {
		margin-left: auto;
		margin-right: auto;
	}
}