.our-supply {
	padding: 30px 0;
	overflow: hidden;

	@media (min-width: 768px) {
		padding: 40px 0;
	}

	@media (min-width: 992px) {
		padding: 60px 0;
	}

	@media (min-width: 1200px) {
		padding: 80px 0;
	}
}

.our-supply__title {
	color: rgb(100, 69, 141);
}

.our-supply__wrapper {
	position: relative;
	width: calc(100% + 1.5rem);
	left: -0.75rem;
	padding-left: 0.75rem;
	margin-top: 30px;

	@media (min-width: 992px) {
		position: static;
		width: 100%;
		left: 0;
		padding: 0;
		margin: 0;
	}

	@media (min-width: 1200px) {
		margin-top: 40px;
	}
}

.supplySwiper {
	overflow: unset;

	.swiper-wrapper {
		padding-top: 30px;
		padding-bottom: 85px;

		@media (min-width: 768px) {
			padding-top: 45px;
			padding-bottom: 95px;
		}

		@media (min-width: 992px) {
			display: flex;
			gap: 30px;
			flex-wrap: wrap;
			padding-bottom: 0;
		}

		@media (min-width: 1400px) {
			gap: 20px;
		}

		@media (min-width: 1700px) {
			padding-top: 60px;
		}

		@media (min-width: 2560px) {
			padding-top: 65px;
		}
	}

	.swiper-slide {
		min-height: 182px;
		width: calc(100% - 60px) !important;

		@media (min-width: 360px) {
			width: calc(100% - 100px) !important;
		}

		@media (min-width: 576px) {
			min-height: 202px;
			width: calc(100% - 200px) !important;
		}

		@media (min-width: 768px) {
			min-height: 212px;
			width: calc(100% - 300px) !important;
		}

		@media (min-width: 992px) {
			min-height: 204px;
			width: calc(100% / 3 - 20px) !important;
			flex-grow: 1;
		}

		@media (min-width: 1200px) {
			min-height: 214px;
		}

		@media (min-width: 1400px) {
			min-height: 236px;
			width: calc(100% / 4 - 20px) !important;
			margin-top: 50px;
		}

		&:nth-child(8n + 4),
		&:nth-child(8n + 3),
		&:nth-child(8n + 2),
		&:nth-child(8n + 1) {
			@media (min-width: 1400px) {
				margin-top: 0;
			}
		}
	}

	.swiper-scrollbar__wrap {
		position: relative;
		width: calc(100% - 130px);
		left: 50%;
		transform: translateX(-52.5%);
		bottom: 40px;

		@media (min-width: 576px) {
			transform: translateX(-51%);
		}

		@media (min-width: 992px) {
			display: none;
		}
	}

	.swiper-scrollbar {
		&::before, &::after {
			width: 70px;
			display: none;

			@media (min-width: 768px) {
				width: 75px;
			}
		}
	}

	.swiper-button-next,
	.swiper-button-prev {
		top: auto;
		bottom: 24px;

		@media (min-width: 992px) {
			display: none;
		}
	}

	.swiper-button-next {
		right: 0.75rem;

		@media (min-width: 576px) {
			right: 0.75rem;
		}
	}

	.swiper-button-prev {
		left: 0;
	}
}

.our-supply__item {
	border: 0.58px solid rgb(230, 221, 238);
	border-radius: 10px;
	padding: 43px 16px 16px;
	position: relative;

	@media (min-width: 768px) {
		padding: 53px 20px 20px;
	}

	@media (min-width: 992px) {
		padding: 53px 20px 5px;
	}

	@media (min-width: 1200px) {
		padding: 60px 20px 15px;
	}

	@media (min-width: 1400px) {
		padding: 75px 24px 30px;
	}

	@media (min-width: 1700px) {
		padding: 75px 30px 30px;
	}
}

.our-supply__icon {
	position: absolute;
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	width: 70px;
	height: 100px;
	top: -32px;
	left: 8px;

	@media (min-width: 768px) {
		width: 120px;
		height: 135px;
		top: -46px;
		left: 14px;
	}

	@media (min-width: 992px) {
		top: -42px;
	}

	@media (min-width: 1200px) {
		top: -30px;
	}

	@media (min-width: 1400px) {
		width: 140px;
		height: 160px;
		top: -48px;
	}
}

.our-supply__item-title {
	font-size: 16px;
	font-weight: 700;

	@media (min-width: 576px) {
		font-size: 22px;
		max-width: 90%;
		line-height: 110%;
	}

	@media (min-width: 768px) {
		font-size: 26px;
	}

	@media (min-width: 992px) {
		font-size: 20px;
	}

	@media (min-width: 1200px) {
		font-size: 26px;
		max-width: unset;
	}

	@media (min-width: 1200px) {
		font-size: 22px;
	}

	@media (min-width: 1700px) {
		font-size: 26px;
	}
}

.our-supply__item-desc {
	font-size: 14px;
	margin-bottom: 0;

	@media (min-width: 576px) {
		font-size: 16px;
		max-width: 90%;
		line-height: 130%;
	}

	@media (min-width: 1200px) {
		max-width: unset;
	}

	@media (min-width: 2560px) {
		font-size: 18px;
	}
}