.business-sites {
	@media (max-width: 991px) {
		overflow: hidden;
	}

	@media (min-width: 992px) {
		overflow: hidden;
	}

	@media (min-width: 1200px) {
		padding-top: 75px;
		margin-top: -35px;
	}

	@media (min-width: 1400px) {
		padding-top: 90px;
		margin-top: -40px;
	}

	@media (min-width: 1600px) {
		margin-top: 0;
		padding-top: clamp(80px, 24vh, 215px) !important;
	}

	@media (min-width: 2560px) {
		padding-top: clamp(215px, 22vh, 325px) !important;
	}

	.container {
		@media (max-width: 575px) {
			overflow: hidden;
		}

		@media (min-width: 1600px) {
			height: 100%;
		}

		& > .row {
			height: 100%;
		}
	}
}

.business-sites__bg {
	padding: 30px 0 20px;
	background-image: url('../img/decor/sites-bg2.png');
	background-repeat: no-repeat;
	background-size: auto;
	background-position: top -495px center;
	height: 100%;
	position: relative;

	@media (min-width: 576px) {
		background-size: auto;
	}

	@media (min-width: 768px) {
		background-position: top -400px center;
	}

	@media (min-width: 1600px) {
		background-position: top center;
		min-height: 725px;
	}

	@media (min-width: 2560px) {
		background-size: cover;
	}

	&::before {
		position: absolute;
		content: '';
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-image: url('../img/decor/success-bg.png');
		background-repeat: no-repeat;
		background-size: 430px;
		background-position: top -8px left calc(50% - 199px);
		pointer-events: none;

		@media (min-width: 375px) {
			left: auto;
			background-size: 390px;
			background-position: top -3px right calc(50% + 180px);
		}

		@media (min-width: 576px) {
			background-position: top -8px right calc(50% + 210px);
		}

		@media (min-width: 768px) {
			background-position: top -8px right calc(50% + 290px);
			background-size: 545px;
		}

		@media (min-width: 992px) {
			background-size: 600px;
			background-position: bottom 8px right calc(50% + 450px);
		}

		@media (min-width: 1200px) {
			background-size: 640px;
			background-position: bottom -83px right calc(50% + 566px);
		}

		@media (min-width: 1400px) {
			background-size: 956px;
			background-position: bottom -128px right calc(50% + 630px);
		}

		@media (min-width: 1600px) {
			background-size: calc(685px + 30vh);
			background-position: bottom calc(50% - 13vh) right calc(50% + 630px);
		}

		@media (min-width: 1700px) {
			background-position: bottom calc(50% - 13vh) right calc(50% + 800px);
		}

		@media (min-width: 2560px) {
			background-size: calc(1200px);
			background-position: top 275px right calc(50% + 800px);
		}
	}
}

.business-sites__title-row {
	@media (min-width: 768px) {
		height: 240px;
	}

	@media (min-width: 1600px) {
		height: 100%;
	}

	[class^="col"]:first-child {
		@media (min-width: 1200px) {
			display: none;
		}
	}
}

.business-sites__titles {
	@media (min-width: 768px) {
		display: flex;
		flex-direction: column;
		justify-content: center;
		height: 100%;
	}

	@media (min-width: 1200px) {
		justify-content: flex-start;
	}

	@media (min-width: 1400px) {
		margin-top: 20px;
	}

	@media (min-width: 1600px) {
		margin-top: 8vh;
	}
}

.business-sites__title,
.business-sites__subtitle {
	color: #fff;
	max-width: 220px;
	margin-bottom: 0;
	min-height: 66px;

	@media (min-width: 768px) {
		max-width: unset;
		min-height: unset;
	}
}

.business-sites__title {
	margin-bottom: 20px;

	@media (min-width: 1400px) {
		white-space: nowrap;
		width: max-content;
	}
}

.business-sites__subtitle {
	font-size: 15px;
	line-height: 18px;

	@media (min-width: 992px) {
		max-width: 260px;
		font-size: 16px;
		line-height: 20px;
	}

	@media (min-width: 1200px) {
		max-width: 310px;
	}

	@media (min-width: 1400px) {
		font-size: 24px;
		line-height: 29px;
		max-width: 490px;
	}
}

.business-sites__slider {
	position: relative;
	height: calc(265px + 40vw);
	margin-top: 20px;
	padding: 20px 0;
	transition: all .2s ease;

	@media (min-width: 576px) {
		height: 518px;
		width: calc(100% + 74px);
	}

	@media (min-width: 768px) {
		height: 570px;
		width: 100%;
	}

	@media (min-width: 1200px) {
		margin-top: -110px;
		width: calc(100% + 20px);
	}

	@media (min-width: 1400px) {
		height: 700px;
	}

	@media (min-width: 1600px) {
		height: calc(290px + 20vh);
	}

	@media (min-width: 1921px) {

	}

	.swiper {
		position: absolute;
		left: 0;
		width: calc(128.5px + 30vw);
		height: calc(196px + 45vw);

		@media (min-width: 425px) {
			left: calc(50%);
			transform: translateX(-80%);
		}

		@media (min-width: 576px) {
			height: 455px;
			transform: none;
			left: 10px;
		}

		@media (min-width: 768px) {
			height: 500px;
			left: 55px;
			width: 360px;
		}

		@media (min-width: 1200px) {
			height: 580px;
			width: 400px;
		}

		@media (min-width: 1400px) {
			height: 715px;
			width: 500px;
		}

		@media (min-width: 1600px) {
			height: calc(570px + 20vh);
			width: calc(280px + 20vh);
		}

		@media (min-width: 1921px) {
			height: calc(570px + 30vh);
			width: calc(280px + 25vh);
		}

	}

	.swiper-slide {
		border-radius: 8px;
		transition: all .3s ease;
		user-select: none;
		background-color: #883ede;
		border: 0.5px solid rgba(255,255,255,0.95);
		box-shadow: 0px 5.97px 14px 0px rgba(0, 0, 0, 0.25);
		overflow-y: scroll;
		-ms-overflow-style: none;  /* IE and Edge */
		scrollbar-width: none;  /* Firefox */
		width: calc(128.5px + 30vw);

		@media (min-width: 576px) {
			width: unset;
		}


		&::-webkit-scrollbar {
			display: none;
		}

		&.swiper-slide-active {
			box-shadow: 0px 5.97px 14px 0px rgba(0, 0, 0, 0.5);
		}

		a {
			display: block;
			width: 100%;
			height: max-content;
			//overflow: hidden;
			border-radius: 8px;
		}

		img {
			//pointer-events: none;
			//user-select: none;
			border-radius: 8px;
			//width: 100%;
			//height: 100%;
			//display: block;

			@media (min-width: 576px) {
				max-width: 100%;
			}
		}
	}


	.swiper-slide:not(.swiper-slide-active) {
		opacity: 0;
		transition: all .3s ease;
	}

	.swiper-slide-active ~ .swiper-slide  {
		opacity: 1 !important;
		filter: brightness(0.65);
		touch-action: none;
		position: relative;
		border-color: #7138f9;

		a {
			position: relative;

			&::before {
				position: absolute;
				content: '';
				top: -2px;
				left: -2px;
				width: calc(100% + 4px);
				height: calc(100% + 4px);
				background: rgb(34,15,138);
				background: linear-gradient(271deg, rgb(51, 0, 173) -2%, rgb(64, 28, 167, 0.51) 22%, rgb(42, 27, 129, 0.88%) 50%);
				opacity: 0.75;
			}
		}

		&:last-child,
		&:nth-last-child(1) {
			box-shadow: 0 0 0 0;
		}

		a, img {
			pointer-events: none;
		}
	}
}