:root {
  --bs-font-family: 'Montserrat', sans-serif; // Семейство шрифта по умолчанию
  --bs-font-size: 15px; // Размер шрифта по умолчанию
  --bs-font-weight: 400; // Жирность шрифта по умолчанию
  --bs-line-height: 120%; // Базовая высота шрифта


  --bs-body-bg: #fff;
  --bs-font-color: #687086; // Цвет шрифта по умолчанию



  --btn-text: #fff;



  --accent-color: #64458d;

  --nav-items: #844bb2;



  --input-border: #bd00ff;
  --input-border-active: #f82eff;
  --input-background: rgba(69, 27, 139, 0.66);
  --input-text: #fff;
  --input-radius: 11px;

  --accent-text: #8f35ba;

  --feature-title: rgb(100, 69, 141);
  --feature-text: rgb(132, 75, 179);


  /*Other*/
  
 

  @media (min-width: 768px) {
    --bs-font-size: 16px;
    --bs-line-height: 156%;

    --main-hero__col-1: 55%;
    --main-hero__col-2: 45%;
  }


  @media (min-width: 992px) {
    --main-hero__col-1: 62%;
    --main-hero__col-2: 38%;
    --input-radius: 15px
  }

  @media (min-width: 1400px) {
    --main-hero__col-1: 56%;
    --main-hero__col-2: 44%;
  }

  @media (min-width: 1600px) {
    --main-hero__col-1: 58%;
    --main-hero__col-2: 42%;
    --input-radius: 20px;
  }
}