.header {
  z-index: 50;
  position: relative;
  //margin-bottom: -92px;
  width: 100vw;
  overflow: hidden;
  transition: all .5s ease .5s;

  &::before {
    --bs-gutter-x: 1.5rem;
    position: absolute;
    content: '';
    bottom: 0;
    height: 1px;
    background-color: #844BB2;
    left: calc(var(--bs-gutter-x) * .5);
    width: calc(100% - var(--bs-gutter-x));
    opacity: 0.2;

    @media (min-width: 768px) {
      width: calc(var(--main-hero__col-1) - var(--bs-gutter-x));
    }

    @media (min-width: 992px) {
      content: none;
    }
  }

  .navbar {
    & > .container {
      @media (min-width: 992px) {
        position: relative;
      }

      &::before {
        @media (min-width: 992px) {
          --bs-gutter-x: 1.5rem;
          position: absolute;
          content: '';
          bottom: -6px;
          height: 1px;
          background-color: #844BB2;
          left: calc(var(--bs-gutter-x) * .5);
          width: calc(var(--main-hero__col-1) - var(--bs-gutter-x) - 8px);
          opacity: 0.2;
        }

        @media (min-width: 1200px) {
          width: calc(var(--main-hero__col-1) - var(--bs-gutter-x) - 22px);
        }

        @media (min-width: 1400px) {
          width: calc(var(--main-hero__col-1) - var(--bs-gutter-x) + 8px);
          bottom: -22px;
        }

        @media (min-width: 1600px) {
          width: calc(var(--main-hero__col-1) - var(--bs-gutter-x) - 20px);
          bottom: -22px;
        }

        @media (min-width: 1700px) {
          width: calc(var(--main-hero__col-1) - var(--bs-gutter-x) - 127px);
        }

        @media (min-width: 1900px) {
          width: calc(var(--main-hero__col-1) - var(--bs-gutter-x) - 128px);
        }
      }
    }
  }

  .navbar-toggler {
    .toggler-icon {
      @media (min-width: 768px) {
        background-color: #fff;
      }
    }
  }

  &--absolute {
    position: absolute;
  }

  @media (min-width: 992px) {
    width: 100%;
  }

  &.header-is-sticky {
    position: fixed;
    top: 0;
    width: 100%;
    //box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
    background-color: var(--bs-body-bg);

    &::before {
      @media (min-width: 768px) {
        width: calc(100% - var(--bs-gutter-x));
      }

      @media (min-width: 992px) {
        content: '';
      }
    }

    .navbar-toggler {
      .toggler-icon {
        @media (min-width: 768px) {
          background-color: var(--accent-color);;
        }
      }
    }

    .navbar {
      & > .container {
        @media (min-width: 992px) {
          position: relative;
        }

        &::before {
          @media (min-width: 992px) {
            content: none;
          }
        }
      }
    }
  }

  &.overflow {
    overflow: visible;
  }

  .navbar {
    padding: 4px 0 1px;

    @media (min-width: 992px) {
      padding: 6px 0 6px;
    }

    @media (min-width: 1400px) {
      padding: 22px 0 22px;
    }

    .collapsing {
      height: 0;
      //overflow: hidden;
      transition: none;
      visibility: hidden;
      opacity: 0;
    }
  }

  &__logo {
    display: flex;
    flex-wrap: nowrap;
    position: relative;
    user-select: none;
    z-index: 11;

    @media (min-width: 1200px) {
      margin-right: 50px;
    }

    @media (min-width: 1200px) {
      margin-right: 50px;
    }

    @media (min-width: 1400px) {
      margin-right: 60px;
    }

    @media (min-width: 1700px) {
      margin-right: 120px;
    }

    span {
      align-self: center;
      font-weight: 700;
      font-size: 15px;
      position: relative;
      top: -1px;
      margin-right: 4px;

      @media (min-width: 992px) {
        font-size: 20px;
        margin-right: 4px;
      }

      @media (min-width: 1400px) {
        font-size: 24px;
        margin-right: 10px;
      }

      &:last-child {
        margin-right: 0;
      }
    }

    img {
      width: 40px;
      height: 40px;
      //width: auto;
      //height: 100%;
      transition: all .2s ease;
      margin-right: 7px;

      @media (min-width: 992px) {
        width: 55px;
        height: 55px;
        margin-right: 8px;
      }

      @media (min-width: 1400px) {
        width: 65px;
        height: 65px;
        margin-right: 16px;
      }
    }
  }

  &__menu {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    transform: translateX(100%);
    background-color: #fff;
    //background: #fda4a4;
    transition: all .3s ease;
    z-index: 8;

    @media (min-width: 992px) {
      position: static;
      width: auto;
      height: auto;
      transform: none;
      opacity: 1;
      background-color: transparent;
      padding-top: 0;
      flex-grow: 1;
    }

    &::before {
      --bs-gutter-x: 1.5rem;
      position: absolute;
      //////content: '';
      top: 53px;
      height: 1px;
      background-color: #844BB2;
      left: calc(var(--bs-gutter-x) * .5);
      width: calc(100% - var(--bs-gutter-x));
      opacity: 0.2;

      @media (min-width: 768px) {
        top: 54px;
      }

      @media (min-width: 992px) {
        content: none;
      }
    }

    .container {
      @media (min-width: 992px) {
        width: auto;
        max-width: 100%;
        padding: 0;
        margin: 0;
        display: flex;
        justify-content: flex-end;
      }
    }
  }

  &__nav {
    display: flex;
    flex-direction: column;
    font-weight: 500;
    text-align: center;
    padding-top: 30px;
    padding-bottom: 4px;

    @media (min-width: 992px) {
      flex-direction: row;
      padding-top: 0;
      padding-bottom: 0;
      margin: 0 auto;
      flex-wrap: wrap;
      flex-grow: 1;
    }

    a, button {
      @media (max-width: 991px) {
        padding: 3px 0;
      }
    }
  }

  &__navItem {
    text-decoration: none;

    @media (min-width: 992px) {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    span {
      transition: all .2s;
      font-weight: 600;
      font-size: 18px;
      text-align: center;
      color: var(--nav-items);
    }

    &.active {
      span {
        border-bottom-color: #d1d1d1;
      }
    }

    &:not(.active):hover {
      span {
        border-bottom-color: #5d55ed;
      }
    }

    &:not(:last-child) {
      @media (min-width: 992px) {
        margin-right: 18px;
      }

      @media (min-width: 1200px) {
        margin-right: 50px;
      }

      @media (min-width: 1700px) {
        margin-right: 57px;
      }
    }
  }

  &.m-menu-open {
    position: fixed;
    touch-action: auto;

    &::before {
      --bs-gutter-x: 1.5rem;
      position: absolute;
      content: "";
      top: 53px;
      height: 1px;
      background-color: #844BB2;
      left: calc(var(--bs-gutter-x)* 0.5);
      width: calc(100% - var(--bs-gutter-x));
      opacity: 0.2;
      z-index: 12;

      @media (min-width: 768px) {
        top: 54px;
      }
    }

    &::after {
      position: absolute;
      //content: ''; //  А нужно ли?
      top: 0;
      left: 0;
      width: 100%;
      height: 53px;
      background-color: #fff;
      z-index: 10;

      @media (min-width: 768px) {
        background-color: transparent;
      }
    }

    .navbar-toggler {
      z-index: 12;

      .toggler-icon {
        background-color: var(--accent-color);;
      }
    }

    .header__menu {
      transform: translateX(0);
      transition: all .3s ease;
      position: fixed;
      touch-action: auto;
    }
  }
}

.header__menu {

  @media (max-width: 991px) {
    overflow-y: scroll;
    touch-action: auto;
    min-height: 440px;
    height: 100%;

    min-height: min-content;
    height: 100%;
  }

  & > .container {
    @media (max-width: 991px) {
      min-height: 440px;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }
}

.header__auth {
  display: flex;
  flex-direction: column;

  @media (min-width: 992px) {
    flex-direction: row;
    flex-wrap: wrap;
    min-width: 320px;
    justify-content: space-between;
  }

  @media (min-width: 1200px) {
    min-width: 370px;
  }

  @media (min-width: 1400px) {
    max-width: 440px;
    width: 100%;
  }

  @media (min-width: 1700px) {
    max-width: 524px;
  }

  .btn {
    font-size: 16px;
    padding: 10px 24px;
    max-height: 46px;
    max-width: fit-content;

    @media (max-width: 991px) {
      margin: 10px auto;
    }

    @media (min-width: 1400px) {
      font-size: 18px;
      padding: 14px 33px;
      max-height: 56px;
    }
  }

  .btn--icon {
    border-radius: 50%;
    min-width: 46px;
    min-height: 46px;
    max-width: 46px;
    max-height: 46px;

    @media (min-width: 1400px) {
      min-width: 56px;
      min-height: 56px;
      max-width: 56px;
      max-height: 56px;
    }
  }

  .tg-icon {
    padding: 0;

    svg {
      top: 52%;
      left: 49%;
      width: 26px;
      height: 26px;

      @media (min-width: 1400px) {
        width: 34px;
        height: 29px;
      }
    }
  }
}

@media only screen
and (max-device-width: 991px) /* Ширина устройства меньше или равна 480px */
and (orientation: landscape) { /* Режим ландшафтной ориентации */

  .header__menu {
    //background-color: red;
  }

  .header__menu > .container {
    min-height: 300px;
  }

  .header__auth {
    flex-direction: row;
    min-width: 400px;
    margin: 0 auto;
  }
}



body[class*="fp-viewing"]:not(.fp-viewing-0) {
  .header--absolute {
    position: fixed;
    top: 0;
    width: 100%;
    //box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
    background-color: var(--bs-body-bg);
    transition: all 0.2s ease;

    &::before {
      @media (min-width: 768px) {
        width: calc(100% - var(--bs-gutter-x));
      }

      @media (min-width: 992px) {
        content: '';
      }
    }

    .navbar-toggler {
      .toggler-icon {
        @media (min-width: 768px) {
          background-color: var(--accent-color);;
        }
      }
    }

    .navbar {
      & > .container {
        @media (min-width: 992px) {
          position: relative;
        }

        &::before {
          @media (min-width: 992px) {
            content: none;
          }
        }
      }
    }
  }
}
