#fp-nav {
  ul {
    li {
      a {
        span {
          height: 7px;
          width: 7px;
          border: 1px solid #ddbfff;
          background-color: #ddbfff;
        }

        &.active {
          span {
            height: 13px !important;
            width: 13px !important;
            margin: -5px 0 0 -5px !important;
            border-color: #ddbfff;
            background-color: #fff;
          }
        }
      }

      &:hover {
        a {
          span {
            width: 9px;
            height: 9px;
            margin: -3px 0 0 -3px;
            border-color: #ddbfff;
            background-color: #fff;
          }
        }
      }
    }
  }
}

.no-m-scrollbar {
  scrollbar-color: transparent transparent;
}
.no-m-scrollbar::-webkit-scrollbar {
  height: 0;
}
.no-m-scrollbar::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 0 rgba(0, 0, 0, 0);
}
.no-m-scrollbar::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0);
  -webkit-box-shadow: inset 0 0 0 rgba(0, 0, 0, 0);
}
.no-m-scrollbar::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(0, 0, 0, 0);
}

.start-invisible {
  opacity: 0;
  transition: all .3s ease;

  &.animate__animated {
    opacity: 1;
    transition: all .3s ease;
  }
}


