.our-features {
	padding: 30px 0;

	@media (min-width: 768px) {
		padding: 40px 0;
	}

	@media (min-width: 992px) {
		padding: 60px 0;
	}

	@media (min-width: 1200px) {
		padding: 80px 0;
	}

	@media (min-width: 1600px) {
		min-height: 860px;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}
}

.our-features__wrap {
	display: flex;
	gap: 20px;
	flex-wrap: wrap;
}

.our-features__title {
	margin-bottom: 0;
	color: var(--feature-title);

	@media (min-width: 1200px) and (max-width: 1399px) {
		font-size: 46px;
	}

	@media (min-width: 1400px) and (max-width: 1699px) {
		font-size: 56px;
	}
}

.our-features__item {
	box-sizing: border-box;
	border: 1px solid rgb(230, 221, 238);
	border-radius: 6.89px;
	padding: 15px 20px;

	&:first-child {
		padding: 14px 20px;
		word-break: break-word;

		@media (min-width: 768px) {
			width: calc(50% + 108px);
		}

		@media (min-width: 992px) {
			width: calc(50% + 148px);
		}

		@media (min-width: 1200px) {
			width: calc(25% - 15px);
		}

		@media (min-width: 1700px) {
			padding: 33px 20px 34px;
		}
	}

	@media (max-width: 767px) {
		&:not(:first-child) {
			width: calc(50% - 10px);
		}

		&:first-child {
			flex-grow: 1;
		}
	}

	@media (min-width: 768px) {
		width: calc(100% / 3 - 14px);
	}

	@media (min-width: 992px) {
		width: calc(100% / 3 - 14px);
		padding: 5px 20px 25px;
	}

	&:last-child {
		@media (max-width: 1199px) {
			flex-grow: 1;
		}
	}

	@media (min-width: 1200px) {
		width: calc(100% / 4 - 15px);
		padding: 5px 20px 25px;
	}

	@media (min-width: 1700px) {
		padding: 20px 20px 34px;
	}
}

.our-features__plate {
	//width: 100%;
	//height: 100%;
	width: 32vw;
	height: 32vw;
	background-repeat: no-repeat;
	background-size: contain;
	background-position: center;
	background-image: url('../img/decor/feature-plate--1.png');
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	justify-content: center;
	transition: all .3s ease;

	@media (min-width: 576px) {
		width: 140px;
		height: 140px;
	}

	@media (min-width: 992px) {
		width: 175px;
		height: 175px;
	}

	@media (min-width: 1700px) {
		width: 12vw;
		height: 12vw;
		max-width: 300px;
		max-height: 300px;
	}

	&--1 {
		background-image: url('../img/decor/feature-plate--1.png');
	}

	&--2 {
		background-image: url('../img/decor/feature-plate--2.png');
	}

	&--3 {
		background-image: url('../img/decor/feature-plate--3.png');
	}

	&--7 {
		background-image: url('../img/decor/feature-plate--4.png');
	}

	&--5 {
		background-image: url('../img/decor/feature-plate--5.png');
	}

	&--6 {
		background-image: url('../img/decor/feature-plate--6.png');
	}

	&--7 {
		background-image: url('../img/decor/feature-plate--7.png');
	}
}

.our-features__icon {
	text-align: center;
	position: relative;
	top: -6px;
	transition: all .3s ease;

	@media (min-width: 1700px) {
		top: -12px;
	}

	width: 7.5vw;
	height: 7.5vw;
	margin-left: auto;
	margin-right: auto;
	background-repeat: no-repeat;
	background-position: center;

	@media (min-width: 576px) {
		width: 40px;
		height: 40px;
	}

	@media (min-width: 1700px) {
		width: 60px;
		height: 52px;
	}

	img {
		width: 7.5vw;
		height: 7.5vw;
		object-fit: contain;
		object-position: center;

		@media (min-width: 576px) {
			width: 40px;
			height: 40px;
		}

		@media (min-width: 1700px) {
			width: 60px;
			height: 52px;
		}
	}
}

.our-features__text {
	font-family: 'Montserrat', sans-serif;
	font-size: clamp(12px, 3vw, 16px);
	font-weight: 700;
	line-height: clamp(10.33px, 3vw, 20px);
	letter-spacing: 0;
	text-align: center;
	color: var(--feature-text);
	margin: -4vw auto 0;
	width: fit-content;

	@media (min-width: 576px) {
		margin: -10px auto 0;
	}

	@media (min-width: 400px) {
		max-width: 29vw;
	}

	@media (min-width: 768px) {
		font-size: 14px;
		margin: -15px auto 0;
	}

	@media (min-width: 992px) {
		max-width: 225px;
		font-size: 16px;
	}

	@media (min-width: 1200px) {
		font-size: 20px;
		font-weight: 700;
		line-height: 24px;
		max-width: 192px;
	}

	@media (min-width: 1700px) {
		max-width: 270px;
	}
}



.fadeInUp {
	opacity: 0; /* Начальное значение непрозрачности */
	transform: translateY(50px); /* Начальное значение смещения по вертикали */
	transition: opacity 0.8s ease, transform 0.8s ease; /* Плавное переходное свойство */

	&.animate {
		opacity: 1; /* Конечное значение непрозрачности */
		transform: translateY(0); /* Конечное значение смещения по вертикали */
	}
}