.tarifs {
	//overflow: hidden;

	@media (min-width: 1600px) and (min-height: 920px) {
		padding-top: clamp(70px, 24vh, 180px) !important;
	}
}

.tarifs__bg {
	background-image: url('../img/decor/tarifs-bg.png');
	background-repeat: no-repeat;
	background-size: auto;
	background-position: top -460px center;
	height: 100%;
	position: relative;

	padding: 45px 0 70px 0;

	@media (min-width: 768px) {
		//padding: 40px 0;
	}

	@media (min-width: 992px) {
		//padding: 60px 0;
	}

	@media (min-width: 1200px) {
		//padding: 80px 0;
	}

	@media (min-width: 1600px) {
		display: flex;
		flex-direction: column;
		justify-content: center;
		background-position: top 0 center;
		background-size: cover;
	}

	&::before {
	  position: absolute;
	  content: '';
		top: -20px;
		left: calc(12vw - 50px);
		width: calc(10vw + 40px);
		height: calc(10vw + 120px);
	  background-color: transparent;
		background-image: url('../img/decor/coins.png');
		background-repeat: no-repeat;
		background-position: top center;
		background-size: contain;

		@media (min-width: 576px) {
			left: calc(50% - 260px);
			width: 120px;
			height: 212px;
		}

		@media (min-width: 768px) {
			left: calc(50% - 365px);
			top: -60px;
		}

		@media (min-width: 992px) {
			left: calc(50% - 480px);
			top: -56px;
			width: 145px;
			height: 230px;
		}

		@media (min-width: 1200px) {
			left: calc(50% - 628px);
			top: -30px;
			width: 268px;
			height: 684px;
		}

		@media (min-width: 1400px) {
			left: calc(50% - 698px);
			width: 300px;
			height: 740px;
		}

		@media (min-width: 1600px) {
			top: calc(12vh - 155px);
			left: calc(50% - 709px);
			width: 30vh;
			height: 82vh;
		}

		@media (min-width: 1921px) {
			top: calc(15vh - 240px);
			left: calc(50% - 890px);
			width: 600px;
			height: 950px;
		}
	}
}

.tarifs__title {
	color: #fff;
	padding-left: 19vw;

	@media (max-width: 374px) {
		font-size: 26px;
	}

	@media (min-width: 375px) {
		padding-left: 24vw;
	}

	@media (min-width: 576px) {
		padding-left: 150px;
	}

	@media (min-width: 1200px) {
		padding-left: 235px;
	}

	@media (min-width: 1400px) {
		padding-left: 300px;
	}

	@media (min-width: 1600px) {
		padding-left: 23%;
	}

	@media (min-width: 1600px) {
		padding-left: 32%;
	}

	@media (min-width: 1921px) {
		padding-left: 515px;
	}
}

.tarifs__wrap {
	@media (min-width: 768px) {
		display: flex;
		gap: 20px;
	}

	@media (min-width: 1200px) {
		padding-left: 235px;
	}

	@media (min-width: 1400px) {
		padding-left: 300px;
	}

	@media (min-width: 1600px) {
		padding-left: 23%;
	}

	@media (min-width: 1600px) {
		padding-left: 32%;
	}

	@media (min-width: 1921px) {
		padding-left: 515px;
	}
}

.tarifs__item {
	margin-top: 20vw;
	padding: 14vw 25px 36px;
	border-radius: 23px;
	box-shadow: 0 32.6px 34.85px 0 rgba(79, 34, 107, 0.21);
	background: #fff;
	position: relative;

	@media (min-width: 425px) {
		padding: 75px 25px 36px;
		margin-top: 85px;
	}

	@media (min-width: 768px) {
		margin-top: 60px;
		display: flex;
		flex-direction: column;
		min-height: 460px;
	}

	@media (min-width: 992px) {
		flex-grow: 1;
		min-height: 420px;
	}

	@media (min-width: 1921px) {
		min-height: 525px;
	}
}

.tarifs__icon-bg {
	width: 45vw;
	height: 45vw;
	background-repeat: no-repeat;
	background-size: contain;
	background-position: center;
	background-image: url("../img/decor/price-plate.png");
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	justify-content: center;
	position: absolute;
	top: calc(-22vw + 20px);
	left: 50%;
	transform: translateX(-50%);

	@media (min-width: 425px) {
		width: 210px;
		height: 210px;
		top: -72px;
	}
}

.tarifs__icon {
	text-align: center;
	position: relative;
	top: -20px;

	img {
		width: 50px;
		height: 50px;
	}

	svg, path {
		fill: #fff;
	}
}

.tarifs__head {
	color: rgb(100, 69, 141);
	font-family: Montserrat, sans-serif;
	font-size: 32px;
	font-weight: 300;
	text-align: center;
	margin-top: 30px;
	margin-bottom: 20px;

	@media (min-width: 1200px) {
		font-size: 48px;
		margin-bottom: 22px;
		line-height: 100%;
		margin-top: 24px;
	}
}

.tarifs__subtitle {
	text-align: center;
	max-width: 320px;
	margin-left:  auto;
	margin-right: auto;

	@media (min-width: 576px) {
		font-size: 16px;
	}

	@media (min-width: 992px) {
		margin-bottom: 0;
		max-width: 330px;
	}

	@media (min-width: 1200px) {
		font-size: 18px;
		max-width: 380px;
		margin-bottom: 25px;
	}

	@media (min-width: 1921px) {
		margin-bottom: 40px;
	}
}

.tarifs__ticket-wrap {
	overflow: hidden;
	padding: 0;
	max-width: 282px;
	margin-left: auto;
	margin-right: auto;

	@media (min-width: 768px) {
		margin-top: auto;
	}

	@media (min-width: 992px) {
		max-width: 345px;
	}

	@media (min-width: 1921px) {
		margin-top: 0;
	}
}

.tarifs__ticket {
	padding: 15px 18px;
	display: flex;
	border: 1px solid rgb(230, 221, 238);
	border-radius: 5px;
	max-width: 280px;
	margin-left: auto;
	margin-right: auto;
	color: rgb(126, 48, 158);
	position: relative;

	@media (min-width: 992px) {
		max-width: 347px;
	}

	@media (min-width: 1200px) {
		font-size: 18px;
		min-height: 104px;
	}

	&::before, &::after {
		position: absolute;
		content: '';
		top: calc(50%);
		width: 40px;
		height: 40px;
		border-radius: 50%;
		//outline: 1px solid rgb(230, 221, 238);
		border: 1px solid rgb(230, 221, 238);
		background-color: #fff;
		z-index: 2;
	}

	&::before {
		transform: translateY(-50%) translateX(50%);
	  left: -48px;
	}

	&::after {
		transform: translateY(-50%) translateX(50%);
		right: -8px;
	}
}

.tarifs__ticket-type {
	padding: 0 15px 0 0;
	text-align: center;
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	justify-content: center;


}

.tarifs__ticket-value {
	padding: 0 0 0 15px;
	text-align: center;
	min-width: fit-content;
	display: flex;
	flex-wrap: wrap;
	font-weight: 700;
	position: relative;
	flex-direction: column;
	justify-content: center;

	&::before {
	  position: absolute;
	  content: '';
	  top: -15px;
	  left: 0;
	  width: 1px;
	  height: calc(100% + 30px);
		border-left: 1px dashed rgb(230, 221, 238);
	}

	span {
		font-family: Montserrat, sans-serif;

		&:first-child {
			height: 36px;
		}

		&:last-child {
			margin-top: -10px;
			font-size: 24px;
			height: 30px;

			@media (min-width: 360px) {
				font-size: 28px;
			}
		}
	}
}

.tarifs__button {
	margin-top: 28px;
	text-align: center;

	@media (min-width: 768px) {
		margin-top: 20px;
	}

	@media (min-width: 1200px) {
		margin-top: 40px;
	}

	.btn {
		box-sizing: border-box;
		border: 1.36px solid rgba(255, 255, 255, 0.29);
		border-radius: 98.25px;
		color: #fff;
		box-shadow: inset 0px -7.47px 7.47px 0px rgba(255, 255, 255, 0.25),0px 13.58px 27.85px 0px rgba(216, 202, 250, 0.85);
		backdrop-filter: blur(19.15px);
		background: linear-gradient(232deg, #0071cd 6.2%, #9b0b9d 100%), #e1eafd;
	}
}