.swiper {
	width: 100%;
	height: 100%;
}

.swiper-slide {
	//text-align: center;
	//font-size: 18px;
	//background: #fff;
	display: flex;
	justify-content: center;
	//align-items: center;
}

.swiper-slide img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.our-offers__slider-wrap {
	width: calc(100% + 1.5rem);
	position: relative;
	left: -0.75rem;
}

.offersSwiper {
	padding-bottom: 84px;

	@media (min-width: 768px) {
		padding-top: 20px;
	}

	@media (min-width: 1200px) {
		padding-bottom: 120px;
	}


	.swiper-wrapper {
		margin-left: 0.75rem;

		@media (min-width: 768px) {
			//margin-left: 1.5rem;
			margin-left: 0;
		}
	}

	.swiper-slide {
		overflow: hidden;
		border-radius: 10px;
		background-color: #fff;
		padding: 20px 20px 20px 20px;
		box-shadow: 0 20px 41px 0 rgba(79, 34, 107, 0.12);
		height: 255px;
		width: calc(100% - 60px) !important;

		@media (min-width: 375px) {
			height: 263px;
			padding: 20px 20px 24px 58px;
		}

		@media (min-width: 576px) {
			height: 280px;
			width: calc(100% - 160px) !important;
		}

		@media (min-width: 768px) {
			height: unset;
			width: calc(100% - 30px) !important;
			padding: 25px 25px 30px 75px;
			min-height: 355px;
		}

		@media (min-width: 992px) {
			width: calc(100% / 2 + 60px) !important;
			padding: 25px 25px 30px 62px;
		}

		@media (min-width: 1200px) {
			width: calc(100% / 2 + 200px) !important;
			padding: 64px 64px 64px 129px;
		}

		@media (min-width: 1400px) {
			width: calc(100% / 2 + 100px) !important;
		}

		@media (min-width: 1600px) {
			width: calc(100% / 2 - 20px) !important;
		}

		@media (min-width: 1700px) {
			width: calc(100% / 2 - 100px) !important;
		}

		@media (min-width: 1900px) {
			width: calc(100% / 2 - 8vw) !important;
		}

		@media (min-width: 2000px) {
			width: calc(100% / 2 - 10vw) !important;
		}

		@media (min-width: 2100px) {
			width: calc(100% / 2 - 220px) !important;
		}

		@media (min-width: 2400px) {
			width: calc(100% / 2 - 15vw) !important;
		}
	}

	.swiper-scrollbar__wrap {
		position: relative;
		width: calc(100% - 130px);
		left: 50%;
		transform: translateX(-50%);

		@media (min-width: 768px) {
			width: 200px;
			transform: translateX(-45%);
		}

		@media (min-width: 1200px) {
			position: relative;
			bottom: -26px;
			width: 400px;
			transform: translateX(-30%);
		}

		@media (min-width: 1400px) {
			transform: translateX(-24%);
		}
	}

	.swiper-scrollbar {
		bottom: -41px;

		&::before, &::after {
			width: 70px;
			display: none;

			@media (min-width: 768px) {
				width: 75px;
			}
		}
	}

	.swiper-button-next,
	.swiper-button-prev {
		top: auto;
		bottom: 24px
	}

	.swiper-button-next {
		right: 0.75rem;

		@media (min-width: 768px) {
			right: calc(50% - 160px);
		}

		@media (min-width: 1200px) {
			right: calc(50% - 350px);
		}

		@media (min-width: 1400px) {
			right: calc(50% - 375px);
		}
	}

	.swiper-button-prev {
		left: 0.75rem;

		@media (min-width: 768px) {
			left: calc(50% - 140px);
		}

		@media (min-width: 1200px) {
			left: calc(50% - 190px);
		}

		@media (min-width: 1400px) {
			left: calc(50% - 170px);
		}
	}
}

.our-offers {
	padding: 30px 0;
	position: relative;
	color: #fff;
	overflow-x: hidden;

	@media (min-width: 576px) {
		padding: 45px 0 20px;
	}

	@media (min-width: 768px) {
		padding: 40px 0 0;
	}

	@media (min-width: 1200px) {
		padding: 80px 0 20px;
	}

	@media (min-width: 1600px) {
		min-height: 860px;
		//max-height: 1000px;

		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	&::before {
	  position: absolute;
	  content: '';
	  top: 0;
	  left: 0;
	  width: 100%;
	  height: 46.5%;
	  background-color: transparent;
		background-image: url('../img/decor/offers-back.png');
		background-position: center;
		background-repeat: no-repeat;
		background-size: 130vw;

		@media (min-width: 768px) {
			height: 100%;
			width: 50%;
		}

		@media (min-width: 1200px) {
			width: 48%;
		}

		@media (min-width: 1700px) {
			width: 42%;
		}
	}

	& > * {
		position: relative;
		z-index: 2;
	}

	.fp-tableCell {
		.row {
			height: 100%;
		}
	}
}

.our-offers {
	& > .row {
		@media (min-width: 1600px) {
			height: 100%;
		}
	}
}

.our-offers__container-fluid {
	@media (min-width: 576px) and (max-width: 767px) {
		max-width: 540px;

		.swiper-wrapper {
			width: calc(100% + 5vw);
		}

		.swiper {
			overflow: unset;
		}
	}
}



.our-offers__col-1 {
	.container {
		@media (min-width: 768px) {
			position: absolute;
			right: calc(50% + 82px);
			max-width: 275px;
		}

		@media (min-width: 992px) {
			right: calc(50% + 205px);
		}

		@media (min-width: 1200px) {
			right: calc(50% + 220px);
			max-width: 350px;
		}

		@media (min-width: 1400px) {
			right: calc(50% + 260px);
			max-width: 400px;
		}

		@media (min-width: 1600px) {
			top: 50%;
			transform: translateY(-60%);
		}

		@media (min-width: 1700px) {
			max-width: 500px;
			right: calc(50% + 310px);
		}
	}

	@media (min-width: 1600px) {
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	@media (min-width: 1700px) {
		width: 27%;
	}
}

.our-offers__col-2 {
	@media (min-width: 1600px) {
		width: calc(73% - 10vw);
		position: absolute;
		right: 0;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	@media (min-width: 1700px) {
		width: calc(80% - 10vw);
	}

	@media (min-width: 1900px) {
		width: 1404px;
		width: calc(80% - 10vw);
		right: auto;
		left: calc(50% - 375px);
	}
}



.our-offers__title {
	@media (min-width: 768px) {
		margin-top: 50px;
	}

	@media (min-width: 992px) {
		margin-top: 45px;
	}

	@media (min-width: 1200px) {
		margin-top: 85px;
	}

	@media (min-width: 1600px) {
		margin-top: 0;
	}
}

.our-offers__subtitle {
	@media (min-width: 1700px) {
		max-width: 400px;
	}
}

.our-offers__slider-card {

}

.offer-card {
	max-width: 100%;
	color: #687086;
	display: flex;
	flex-direction: column;
}

.offer-card__type	{
	display: inline-flex;
	background: #323d5b;
	border-radius: 50px;
	padding: 2px 9px;
	font-weight: 700;
	font-size: 11px;
	color: #fff;
	margin-bottom: 7px;
	max-width: fit-content;

	@media (min-width: 576px) {
		margin-bottom: 10px;
	}

	@media (min-width: 768px) {
		font-size: 13px;
		margin-bottom: 12px;
	}

	@media (min-width: 1200px) {
		font-size: 16px;
	}

	&--leads {
		background: #218ff3;
	}

	&--accepeted {
		background: #FF8C38;
	}
}

.offer-card__header {
	margin-bottom: 13px;
	display: flex;
	position: relative;
}

.offer-card__icon {
	min-width: 29px;
	min-height: 29px;
	max-width: 29px;
	max-height: 29px;
	position: relative;
	top: 2px;
	left: 0;

	@media (min-width: 375px) {
		top: 4px;
		position: absolute;
		left: -38px;
	}

	@media (min-width: 768px) {
		min-width: 35px;
		min-height: 35px;
		max-width: 35px;
		max-height: 35px;
		left: -42px;
	}

	@media (min-width: 1200px) {
		min-width: 50px;
		min-height: 50px;
		max-width: 50px;
		max-height: 50px;
		left: -90px;
	}

	svg, img {
		width: 100%;
		height: 100%;
	}
}

.offer-card__title {
	font-weight: 700;
	font-size: 16px;
	line-height: 102%;
	color: #323d5b;
	margin-bottom: 0;
	padding-left: 10px;

	@media (min-width: 375px) {
		font-size: 18px;
		padding-left: 7px;
	}

	@media (min-width: 576px) {
		font-size: 22px;
	}

	@media (min-width: 768px) {
		font-size: 24px;
	}

	@media (min-width: 1200px) {
		font-size: 32px;
		margin-bottom: 22px;
		padding-left: 0;
	}
}

.offer-card__text {
	font-weight: 400;
	font-size: 12px;
	line-height: 120%;
	color: inherit;
	margin-bottom: auto;

	@media (min-width: 576px) {
		font-size: 14px;
	}

	@media (min-width: 768px) {
		font-size: 15px;
	}

	@media (min-width: 1200px) {
		font-size: 16px;
		line-height: 156%;
	}
}

.offer-card__info {
	display: flex;
	justify-content: space-between;

	&:not(:last-child) {
		margin-bottom: 4px;
	}

	& > * {
		color: inherit;
	}

	p {
		flex-grow: 1;
		white-space: nowrap;
		font-weight: 700;
		font-size: 12px;
		margin-bottom: 0;
		max-width: fit-content;

		@media (min-width: 576px) {
			font-size: 14px;
		}

		@media (min-width: 1200px) {
			font-size: 16px;
		}

		&:last-of-type {
			text-align: right;
		}
	}

	span.separator {
		margin: 0 8px;
		opacity: 0.2;
		overflow: hidden;
		font-weight: 700;
		font-size: 12px;
		border-bottom: 1px dotted #687086;
		flex-grow: 1;
		position: relative;
		height: 2px;
		top: calc(100% - 7px);

		@media (min-width: 375px) {
			margin: 0 10px;
		}

		@media (min-width: 576px) {
			margin: 0 15px;
		}
	}
}

.offer-card__info-icon {
	position: relative;
	padding-left: 16px;

	@media (min-width: 375px) {
		padding-left: 20px;
	}

	&::before {
	  position: absolute;
	  content: '';
	  top: 50%;
		transform: translateY(-50%);
	  left: 0;
		width: 9px;
		height: 13px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain;
	}

	&--region {
		&::before {
			background-image: url('../img/icons/pin.svg');
		}
	}

	&--cpl {
		&::before {
			background-image: url('../img/icons/cpl.svg');
		}
	}
}

.our-offers__btn {
	@media (min-width: 375px) {
		max-width: fit-content;
	}
}